import React from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Button, Progress, WhiteSpace } from 'antd-mobile'

import './SuccessComponent.css'

import { GetBrowserType } from '../../Utilities'

class Index extends React.Component {
  handleComplete = () => {
    console.log('complete')
    switch(GetBrowserType()) {
      case 'wechat': 
        window.wx.closeWindow()
        return
      case 'app':
        window.location.href = 'patientpci://patient/main'
        return
      default:
        return
    }
  }

  handleValidate = () => {
    console.log('validate')
    switch(GetBrowserType()) {
      case 'wechat': 
        window.location.href = '/pci-wechat/EMR'
        return
      case 'app':
        window.location.href = 'patientpci://patient/auth'
        return
      default:
        return
    }
  }

  render() {
    document.title = '100%'
    const { globalReducer } = this.props
    const validate = globalReducer.userInfo && globalReducer.userInfo.validate
    return validate ? (
      <div className='success__container'>
        <Progress percent={100} position='normal' /> 
        <div className='success__image'>
          <img style={{width: '2rem'}} src={`${process.env.PUBLIC_URL}/images/img_complete@3x.png`} alt=""/>
        </div>
        <p className='success__content'></p>
        <div className='success__buttons'>
          <Button type='primary' onClick={this.handleComplete}></Button>
        </div>
      </div>
    ) : (
      <div className='success__container'>
        <Progress percent={100} position='normal' /> 
        <div className='success__image'>
          <img style={{width: '2rem'}} src={`${process.env.PUBLIC_URL}/images/img_unauth@3x.png`} alt=""/>
        </div>
        <p className='success__content'></p>
        <div className='success__buttons'>
          <Button type='primary' onClick={this.handleValidate}></Button>
          <WhiteSpace size='lg' />
          <Button type='ghost' onClick={this.handleComplete}></Button>
        </div>
      </div>
    )
  }
}

export default withRouter(connect(state => state)(Index))



