import React from 'react'
import { Button } from 'antd-mobile'

class Index extends React.Component {
  render() {
    const { type, ifStart, ifEnd, onClick, disabled } = this.props
    if (type === 'selection' && !ifStart) {
      return (
        <Button type='ghost' onClick={onClick.bind(this, 'previous')}></Button>
      )
    } else if (type !== 'selection' && ifStart) {
      return (
        <Button type='primary' onClick={onClick.bind(this, 'next')} disabled={disabled}></Button>
      )
    } else if (type !== 'selection' && !ifStart) {
      return (
        <React.Fragment>
          <Button 
            type='ghost' 
            className='question__botton'
            onClick={onClick.bind(this, 'previous')}
          ></Button>
          <Button 
            type='primary' 
            className='question__botton'
            onClick={onClick.bind(this, ifEnd ? 'submit' : 'next')}
            disabled={disabled}
          >{ ifEnd ? '' : ''}</Button>
        </React.Fragment>
      )
    }
  }
}

export default Index



