import React from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Button } from 'antd-mobile'

import './HomeComponent.css'

import { GetBrowserType } from '../../Utilities'

class Index extends React.Component {
  handleImplant = () => {
    const store = this.props
    const router = this.props.history
    const { userId } = store.globalReducer
    router.push(`/question/0${userId ? `?userId=${userId}`: ''}`)
  }

  handleUnImplant = () => {
    switch(GetBrowserType()) {
      case 'wechat': 
        window.wx.closeWindow()
        return
      case 'app':
        window.location.href = 'patientpci://patient/main'
        return
      default:
        return
    }
  }

  render() {
    document.title = ""
    return (
      <div className='home__container'>
        <p className='home__title'><br/></p>
        <div className='home__button'>
          <Button type='primary' onClick={this.handleImplant}></Button>
        </div>
        <div className='home__button'>
          <Button type='ghost' onClick={this.handleUnImplant}></Button>
        </div>
      </div>
    )
  }
}

export default withRouter(connect(state => state)(Index))



